.Youtube {
	width: 100%;
    color: #000;
	.youtubeBanner {
		width: 100%;
		height: 200px;
		border-bottom: 5px solid #000;
		margin: 100px 0px;
		.bannerTitle {
			font: bold 70px/1 'Arapey';
		}
		.bannerText {
			font: 40px/1 'Arapey';
		}
	}
	.youtubeOption {
		width: 100%;
		display: flex;
		justify-content: center;
		margin-bottom: 200px;
		.optionPic {
			width: 45%;
			height: 300px;
			background: url(../../../public/img/youtube/pic1.jpeg);
            box-shadow: 12px 12px 2px 1px #ddd;
		}
		.optionsBox {
			width: 50%;
			position: relative;
			left: 5%;

			.optionTitle {
				font: bold 40px/1 'Arapey';

				position: relative;
			}
			.optionText {
				font: bold 25px/1.3 'Arapey';
			}
		}
	}
	.options {
		width: 100%;
		display: flex;
		justify-content: space-around;
		align-items: center;
		.option1 {
			width: 100%;
			.option1Pic {
				width: 300px;
				height: 300px;
				background: url(../../../public/img/youtube/youtubepic0.png);
				background-size: cover;
				background-position-x: -100px;
				box-shadow: 12px 12px 2px 1px #ddd;
			}
			.option1Text {
				width: 300px;
				height: 300px;
				p {
					text-align: center;
					font: 20px/1 'Arapey';
					margin-top: 30px;
				}
			}
		}
		.option2 {
			width: 100%;
			.option2Pic {
				width: 300px;
				height: 300px;
				background: url(../../../public/img/youtube/youtubepic1.png);
				background-size: cover;
				background-position-x: -140px;
				box-shadow: 12px 12px 2px 1px #ddd;
			}
			.option2Text {
				width: 300px;
				height: 300px;
				p {
					text-align: center;
					font: 20px/1 'Arapey';
					margin-top: 30px;
				}
			}
		}
		.option3 {
			width: 100%;
			.option3Pic {
				width: 300px;
				height: 300px;
				background: url(../../../public/img/youtube/youtubepic2.png);
				background-size: cover;
				background-position-x: -270px;
				box-shadow: 12px 12px 2px 1px #ddd;
			}
			.option3Text {
				width: 300px;
				height: 300px;
				p {
					text-align: center;
					font: 20px/1 'Arapey';
					margin-top: 30px;
				}
			}
		}
		.option4 {
			width: 100%;
			.option4Pic {
				width: 300px;
				height: 300px;
				background: url(../../../public/img/youtube/youtubepic3.png);
				background-size: cover;
				background-position-x: -150px;
				box-shadow: 12px 12px 2px 1px #ddd;
			}
		}
		.option4Text {
			width: 300px;
			height: 300px;
			p {
				text-align: center;
				font: 20px/1 'Arapey';
				margin-top: 30px;
			}
		}
	}
	.optionvideo {
		width: 100%;

		.videoTitle {
			display: block;
			text-align: center;
			font: bold 50px/1 'Arapey';
		}
		.videoText {
			display: block;
			text-align: center;
			font: 20px/1 'Arapey';
			margin: 20px;
		}
	}

	.inner {
		width: 1180px;
		height: 100%;
		&::after {
			content: '';
			display: block;
			clear: both;
		}
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		h1 {
			width: 100%;
		}
		article {
			margin-left: 20px;

			.pic {
				width: 300px;
				height: 150px;
				margin: 20px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					background: rgb(78, 119, 107);
					box-shadow: 12px 12px 2px 1px #ddd;
					transition: 1s;
					&:hover {
						transform: scale(1.1);
					}
				}
			}
		}
	}
}
